import { Swiper, SwiperSlide } from 'vue-awesome-swiper'

export default {
  components: {
    Swiper,
    SwiperSlide,
    contactBtn: require('~/components/common/contact-btn.vue').default,
  },
  props: {
    title: {
      type: String,
      default: null,
    },
    isShowMore: {
      type: Boolean,
      default: true,
    },
    portfolioList: {
      type: Array,
      default () {
        return []
      }
    },
    link: {
      type: String,
      default: null
    }
  },
  data () {
    return {
      swiperOption: {
        slidesPerView: 3,
        spaceBetween: 30,
        // loop: true,
        // lazy: true,
        autoplay: {
          disableOnInteraction: false
        },
        breakpoints: {
          1024: {
            slidesPerView: 3,
            spaceBetween: 30,
          },
          800: {
            slidesPerView: 2,
            spaceBetween: 30,
          },
          340: {
            slidesPerView: 1,
            spaceBetween: 0,
          },
          320: {
            slidesPerView: 1,
            spaceBetween: 0,
          }
        },
        pagination: {
          el: '.swiper-pagination',
          clickable: true
        }
      }
    }
  },
  mounted() {
    // scroll down then play
    if (!!window.IntersectionObserver) {
      let observer = new IntersectionObserver(
        (entries, observer) => {
          entries.forEach((entry) => {
            this.swiper.autoplay.stop()
            if (entry.isIntersecting) {
              // console.log(entry)
              this.swiper.autoplay.start()
              
              // You can remove the observer if you do not need it
              observer.unobserve(entry.target)
            }
          });
        },
        { rootMargin: "0px 0px -200px 0px" }
      );

      // Add the observer to you swiper
      observer.observe(document.querySelector(".portfolio"))
    } else {
      // You can use the polyfill or just start the autoplay
    }
  },
  computed: {
    swiper () { return this.$refs.mySwiper.$swiper },
  },
  methods: {

  },
  watch: {
    
  }
}
